import {
	regExpTel,
} from '../utils/index.js'
export default {
	components: {},
	data() {
		return {
			phone: '',
			password: '',
			code: '',
			yqmvalue: '',
			uservalue: '',
			btnnum: 0,
			codeMsg: '发送验证码',
		};
	},
	methods: {
		getdata() {
			uni.showLoading({
				title: '加载中'
			});
			if (!regExpTel(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
				});
				return false
			} else if (!this.password) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
				});
				return false
			}

			// 请求后台登录接口
			// util.sendPost('', data).then(res){

			// }
			const userinfo = {
				token: 'tonkedddddddddd',
				account: this.phone.substring(0, 3) + "****" + this.phone.substring(7),
				photo: 'touxiang',
				inviteCode: '1234',
			}
			uni.setStorageSync('accountInfo', userinfo);
			// 延时关闭 加载中的 loading框
			setTimeout(() => {
				uni.hideLoading()
			}, 2000)

			uni.switchTab({
				url: '/pages/index/index'
			});
			// this.$router.push('/pages/index/index')
			// weqweououio

		},
		sendPhone() {
			if (this.codeMsg != '发送验证码') {
				uni.showToast({
					title: '请稍后再发送验证码',
					icon: 'none',
				});
			} else if (!regExpTel(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: 'none',
				});
			} else {
				let time = 60
				console.log(time)
				this.timer = setInterval(() => {
					if (time <= 1) {
						this.codeMsg = '发送验证码'
						clearInterval(this.timer)
					} else {
						time--
						this.codeMsg = '已发送（' + time + '）'
					}
				}, 1000)

			}
		},
		change(e) {
			this.btnnum = e
			// console.log(this.btnnum)
		},
		linkforget() {
			uni.navigateTo({
				url: '/pages/index/forget'
			});
		}
	}
};
